<template>
  <div class="report-wrap">
    <app-header
      title="数据报表"
      showBack="true"
    ></app-header>
    <div class="report-main-wrap">
      <div class="report-tabel">
        <div class="t-head">
          <span>日期</span>
          <span>油耗</span>
          <span>里程</span>
          <span>百公里油耗</span>
          <span>累计里程</span>
        </div>
        <div v-if="showd" class="t-body">
          <div
            v-for="(item, index) in report"
            :key="index"
            class="line">
            <span>{{item.date}}</span>
            <span>{{item.fuelConsumption}} <em>L</em> </span>
            <span>{{item.mileage}} <em>km</em></span>
            <span>{{item.lp100km}} <em>L</em></span>
            <span>{{item.totalMileage}} <em>km</em></span>
          </div>
        </div>
        <div  v-else class="t-body">
          <div v-show ="xianshi">
            <h2>暂无数据</h2>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../../components/header/header.vue'
import {fetchData} from '../../../../api/base'
import {TrackUrl} from '../../../../config/urls'
export default {
  name: 'Track-Report',
  data () {
    return {
      report: '',
      showd: false,
      xianshi: false
    }
  },
  created () {
    this.$indicator.open({
      text: '加载中'
    })
    this.getDatas()
  },
  beforeRouteLeave (to, from, next) {
    this.$indicator.close()
    next()
  },
  methods: {
    getDatas () {
      fetchData('post', TrackUrl.trackGetReport, {
        vhcle: this.$route.params.id
      })
        .then(data => {
          this.report = data.data.list2
          if (!this.report) {
            this.xianshi = true
          }
          this.$indicator.close()
        })
        .catch(() => {
          this.$indicator.close()
        })
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .report-wrap{
    height: 100%;
    .report-main-wrap{
      width: 100%;
      height: 100%;
      position: fixed;
      padding-top: 50px;
      overflow: hidden;
      background-color: #fff;
      .report-tabel{
        padding: .6rem;
        padding-top: 0;
        .t-head{
          display: flex;
          border-bottom: 1px solid #ddd;
          text-align: center;
          span{
            flex: 1;
            display: inline-block;
          }
        }
        .t-body{
          .line{
            display: flex;
            padding: .6rem 0;
            text-align: center;
            span{
              flex: 1;
              font-size: 13px;
              em {
                font-size: .5rem;
                font-style: normal;
              }
            }
          }
        }
      }
    }
  }
</style>



